import React, { useEffect } from "react";
import { connect } from "react-redux";
import { useParams } from "react-router-dom";

import { checkMobile } from "helper/helper";
import { getGameIdNumber } from "helper/game";
import { LayoutLottery } from "components";
import { BoxInfoAndResultOrder, BoxOrderPlayGame, MyLottrey } from "moduls";
import { COLUNMS_HISTORY_LOTTO_2, COLUNMS_HISTORY_LOTTO_1 } from "config/table";
import "./style.scss";

const COLUNMS_LOTTO = [
  COLUNMS_HISTORY_LOTTO_1[1],
  COLUNMS_HISTORY_LOTTO_1[2],
  {
    title: "번호",
    dataIndex: "exacly",
    key: "exacly",
    align: "center",
    render: (arr) => (
      <>
        {(arr || []).map((__i, ind) => {
          return (
            <span key={`______key-exacly-${ind}`}>
              <span className={__i?.isExacly ? "bold" : ""}>
                {__i?.number || ""}
              </span>
              {__i?.leg == ind ? "" : `, `}
            </span>
          );
        })}
      </>
    ),
  },
  ...COLUNMS_HISTORY_LOTTO_2,
];
const GamePowerball = ({
  //state
  result,
  history,
  orderItem,
  infoGame,
  loading,
  endRoundOfGame,
  roundofgame,

  //func
  getResult,
  getDataHistory,
  onRemoveItemSelectd,
  onGetInfoGame,
  onBetGameOrder,
  onRandomNumberMinMax,
  onSentResultOptions,
  getUserInfo,
  reLoadDataGame,

  // history order
  InquireLotto,
  count,
  getMyInquireLotto,
}) => {
  const isMobile = checkMobile();
  const typeGame = useParams();
  const type = "POWERBALL";

  const startUpdateInfoGame = () => {
    onGetInfoGame({
      typeGame: typeGame.type,
      type: type,
    }).then(() => {
      // setTimeout(startUpdateInfoGame, 5000);
    });
  };

  useEffect(() => {
    getUserInfo();
  }, []);
  useEffect(() => {
    if (typeGame?.type) {
      startUpdateInfoGame();
    }
  }, [typeGame]);

  const onGetDataCh = async (payload = {}) => {
    const gameid = getGameIdNumber({ typeGame: typeGame.type, type: type });

    await getMyInquireLotto({
      ...payload,
      gameid: gameid,
    });
  };
  return (
    <LayoutLottery>
      <div className="container-cst mt-3 pb-5">
        <div className="page-game">
          {!isMobile && (
            <BoxInfoAndResultOrder
              result={result}
              history={history}
              getResult={getResult}
              getDataHistory={getDataHistory}
              type={type}
              typeGame={Number(typeGame.type) || 5000}
              reLoadDataGame={reLoadDataGame}
              endRoundOfGame={endRoundOfGame}
              roundofgame={roundofgame}
            />
          )}

          <div style={{ height: 10 }} />

          <BoxOrderPlayGame
            orderItem={orderItem}
            onRemoveItemSelectd={onRemoveItemSelectd}
            typeGame={typeGame}
            infoGame={infoGame}
            onBetGameOrder={onBetGameOrder}
            loading={loading}
            onRandomNumberMinMax={onRandomNumberMinMax}
            onSentResultOptions={onSentResultOptions}
            result={result}
            getResult={getResult}
            isMobile={isMobile}
            history={history}
            type={type}
            getDataHistory={getDataHistory}
            reLoadDataGame={reLoadDataGame}
            endRoundOfGame={endRoundOfGame}
            roundofgame={roundofgame}
          />

          <MyLottrey
            hiddenResult
            columns={COLUNMS_LOTTO}
            onGetDataChange={onGetDataCh}
            data={InquireLotto}
            count={count}
            _pageSize={10}
          />
        </div>
      </div>
    </LayoutLottery>
  );
};

export default connect(
  (state) => {
    const { powerball, history } = state;
    return {
      result: powerball?.result || {},
      history: powerball?.history || [],
      orderItem: powerball?.orderItem || [],
      infoGame: powerball?.infoGame || {},
      loading: powerball?.loading,
      endRoundOfGame: powerball?.endRoundOfGame,
      // roundofgame: powerball?.infoGame?.roundofgame || 0,
      roundofgame: powerball?.infoGame?.roundofdate || 0,

      //history
      InquireLotto:
        (state.whitelistedTypeGames || state.whitelistedTypes
          ? history?.InquireLotto.filter(
              ({ typeGame, type }) =>
                (state.whitelistedTypeGames?.has(typeGame) ?? true) &&
                (state.whitelistedTypes?.has(type) ?? true)
            )
          : history?.InquireLotto) || [],
      count: history?.count || 0,
    };
  },
  ({
    powerball: {
      getResult,
      getDataHistory,
      onRemoveItemSelectd,
      onGetInfoGame,
      onBetGameOrder,
      onRandomNumberMinMax,
      onSentResultOptions,
      reLoadDataGame,
    },
    auth: { getUserInfo },
    history: { getMyInquireLotto },
  }) => {
    return {
      getResult,
      getDataHistory,
      onRemoveItemSelectd,
      onGetInfoGame,
      onBetGameOrder,
      onRandomNumberMinMax,
      onSentResultOptions,
      getUserInfo,
      reLoadDataGame,
      getMyInquireLotto,
    };
  }
)(GamePowerball);
